import React, { Component, lazy } from 'react'
import Select from 'react-select';
import {
  CCard,
  CCardBody,
  CCardHeader
} from '@coreui/react'
import {subdit,komponen,subkomponen,anggaran} from './Sourcedanaku.js'

class Danaku extends Component {
  constructor(props) {
    super(props);
    this.state = {
    subdit: null,
    komponen : null,
    subkomponen : null,
    anggaran : null,  
    };
  }
      handleReactSelect = (selectedOption, { name }) => {
        if(name == "subdit") {
            this.setState({ subdit : selectedOption.value});  
          } else if (name == "komponen") {
            this.setState({ komponen : selectedOption.value});
          } else if (name == "subkomponen") {
            this.setState({ subkomponen : selectedOption.value});
          } 
        }

  render(){ 
    const komponenfiltered = komponen.filter((item) => {
      var satuan = item.value
      return satuan.substring(0,10) == (this.state.subdit)
    })
    const subkomponenfiltered = subkomponen.filter((item) => {
      var satuan = item.value
      return satuan.substring(0,12) == (this.state.komponen)
    }) 
    const anggaranfiltered = anggaran.filter((item) => {
      var satuan = item.value
      return satuan.substring(0,14) == (this.state.subkomponen)
    })           
    console.log("ini subdit", this.state.subdit)
    console.log("ini komponen", this.state.komponen)
    console.log("ini subkomponen", this.state.subkomponen)  
    return(
      <CCard>
        <CCardHeader className="bg-light text-dark" >
          <h5>Entry Manifest</h5>
        </CCardHeader>      
        <CCardBody>
            <Select name="subdit" onChange ={this.handleReactSelect.bind(this)} options={subdit} placeholder="Subdit Apa ?"/>
            <Select name="komponen" onChange ={this.handleReactSelect.bind(this)} options={komponenfiltered} placeholder="Komponen Apa ?"/>
            <Select name="subkomponen" onChange ={this.handleReactSelect.bind(this)} options={subkomponenfiltered} placeholder="Subkomponen Apa ?"/>
            <Select name="anggaran" options={anggaranfiltered} placeholder="Anggaran Apa ?"/>
        </CCardBody>
      </CCard>
      )
  }  
}

export default Danaku
